





















































import {Component, Emit, Prop, Vue, Watch} from "vue-property-decorator";
@Component
export default class DialogSystem extends Vue {
    @Prop({
        required: true
    })
    public value:any;

    public style: any =  {
        borderRadius: '6px'
    };

    @Prop({
        type: String,
        default:'Desea continuar con la accion?',
        required: false
    })
    public text!:string;

    @Prop({
        type: String,
        default:'Estas acciones no se pueden redeshacer',
        required: false
    })
    public subtext!:string;

    @Prop({
        type: String,
        default:'',
        required: false
    })
    public cancelText!:string;

    @Prop({
        type: String,
        default:'',
        required: false
    })
    public confirmText!:string;
    @Prop({
        default: 'blue',
        required: false
    })
    public color: any;

    public hoverCancel = true;
    public hoverConfirm = true;

    get ifcancel() {
        if (this.cancelText !== ''){
            return true;
        } else {
            return false;
        }
    }

    get ifconfirm() {
        if (this.confirmText!== '') {
            return true;
        } else {
            return false;
        }
    }
    public mounted() {
        console.log(this.$slots)
    }
    public overbutton(typeAction: string) {
        if (typeAction === 'cancel') {
            this.hoverCancel = false;
        } else {
              this.hoverConfirm = false;
        }
    }
    public leave(typeAction: string) {
        if (typeAction === 'cancel') {
            this.hoverCancel = true;
        } else {
            this.hoverConfirm = true
        }
    }

    @Emit('confirmar')
    public confirm() {

    }

    @Emit('cancelar')
    public cancel() {

    }
}
