















import {Component, Emit, Prop, Vue, Watch} from "vue-property-decorator";
@Component
export default class DInput extends Vue {
    @Prop({
        required: true
    })
    public value:any;

    @Prop({
        default: 'dodgerblue',
        required: false
    })
    public color: any;

    @Prop({
        type: String,
        default: 'text',
        required: false
    })
    public type: any;

    @Prop({
        type: String,
        default: '',
        required: false
    })
    public placeholder: any;
    @Prop({
        type: String,
        default: '',
        required: false
    })
    public name: any;

    @Prop({
        type: String,
        default: '',
        required: false
    })
    public icon: any;

    public iconType: string = '';
    public iconI: string = '';
    public coloricon: any  = {
            color: this.color,
            border: '1px solid ' + this.color,
            height: '25px',
            padding : '',
            textAlign: 'center',
            borderTopRightRadius: '3px',
            borderBottomTightRadius: '3px',
        };

    get colorText() {
      return   'border: 1px solid ' + this.color;
    }

    public isFontAwesome5(iconType: string): boolean {
        return ['fas', 'far', 'fal', 'fab'].some(val => iconType.includes(val))
    }

    public mounted() {
        this.createicon(this.icon);
    }
    public createicon(icon: string) {


        // Material Icon delimiter is _
        // https://material.io/icons/
        const delimiterIndex = icon.indexOf('-');
        const isMaterialIcon = delimiterIndex <= -1;

        if (isMaterialIcon) {
            this.iconType = 'material-icons';
            this.iconI = icon
            // Material icon uses ligatures.

        } else {
            this.coloricon.padding = '5px';
            this.iconType = icon
        }
    }

}
